import * as React from "react"
import { useRef, useLayoutEffect, useState, useReducer } from "react"
import Layout from "../components/layout/layout"
import Button from "../components/button/button"
import { dataCompetence } from "../components/competence/dataCompetence"
import { useInView } from "react-intersection-observer"
import { gsap } from "gsap"
import "./home.scss"
import Competence from "../components/competence/Competence"
import { useEffect } from "react"






const initialState = {

  parallaxPos: { x: 0, y: 0 },

}

function reducer(state, action) {
  switch (action.type) {


    case "CHANGE/COORDINATES":
      return { ...state, parallaxPos: action.payload }
      break;



    default:
      throw new Error("Message")

  }
}


const Home = () => {
  const app = React.useRef()
  const animeTimeLine = useRef()
  const [state, dispatch] = useReducer(reducer, initialState)
  const timelinetitle = useRef()
  const imgprofilRef = useRef(null)
  const [cananime, setcananime] = React.useState(true)
  const [nameOpacity, setnameOpacity] = React.useState(false)
  const [mousevisible, setmousevisible] = useState(false)


  const { ref: titleRef, inView, entry } = useInView({
    /* Optional options */
    threshold: 0,
  });

  const { ref: dividerRef, inView: dividerView, entry: dividerEnty } = useInView({
    /* Optional options */
    threshold: 0.8,
  });



  // Func Img

  const parallax = (event) => {
    const speed = -10

    const x = (window.innerWidth - event.pageX * speed) / 100
    const y = (window.innerHeight - event.pageY * speed) / 100


    dispatch({
      type: "CHANGE/COORDINATES",
      payload: { x: x, y: y }
    })
  }

  const handleMouseEnter = () => {
    imgprofilRef.current.addEventListener("mousemove", parallax)
  }
  const handleMouseLeave = () => {
    imgprofilRef.current.removeEventListener("mousemove", parallax)

    dispatch({
      type: "CHANGE/COORDINATES",
      payload: initialState.parallaxPos
    })

  }





  useLayoutEffect(() => {

    const ctx = gsap.context(() => {
      timelinetitle.current = gsap.timeline()
        .from(".competence-title", {
          duration: 0.8,
          y: 200,
          skewY: 10,
          opacity: 0
        }).paused(true)

    }, app)



    return () => {
      ctx.revert()
    };
  }, [app])


  useLayoutEffect(() => {

    const ctx = gsap.context(() => {
      animeTimeLine.current = gsap.timeline()
        .to(".header-btn-border", {
          duration: 2,
          clipPath: "polygon(0% 0%,100% 0%,100% 100%,0% 100%)",
          ease: "power2.easeIn"

        }).to(".header-text-name", {
          height: "260px",
          ease: "power2.easeIn"
        }, "-=1.6")




    }, app)



    return () => {
      ctx.revert()
    };
  }, [app])

  useEffect(() => {
    function start() {

      const competence = document.querySelector(".competence-title")

      if (inView === true && cananime === true) {

        competence.style.opacity = 1
        competence.style.transform = "translateY(-50px) skewY(0deg)"
        setcananime(false)
      } else {
        if (cananime === true) {
          competence.style.opacity = 0
          competence.style.transform = "translateY(100px) skewY(10deg)"
        }

      }
      console.log(inView);
    }
    start()
  }, [inView])

  useEffect(() => {
    function start() {
      const projetContainer = document.querySelector(".projet-container")
      const divider = document.querySelectorAll(".img-divider")[0]

      if (dividerView === true) {


        setnameOpacity(true)

        divider.style.width = "100vw"
        divider.style.height = "120vh"
        divider.style.transform = "translateY(-40vh)"
        setmousevisible(true)

        setTimeout(() => {
          projetContainer.style.opacity = "1"
        }, 500);




      } else {
        setmousevisible(false)
        setnameOpacity(false)
        divider.style.position = "static"
        divider.style.width = "60%"
        divider.style.height = "500px"
        divider.style.transform = "translateY(0%)"
        projetContainer.style.opacity = "0"

      }
      console.log(dividerView);
    }

    start()
  }, [dividerView])



  return (
    <>
      <div className="fixed-back">
        <div className="fixed-experiences-text">back</div>
        <div className="projet-carousel-slide">
           <div className="projet-1 projet-card"></div>
           <div className="projet-2 projet-card"></div>
           <div className="projet-3 projet-card"></div>
           <div className="projet-4 projet-card"></div>
           <div className="projet-5 projet-card"></div>
        </div>
        
      </div>
      <Layout nameTransparent={nameOpacity}>


        <div ref={imgprofilRef} onMouseEnter={() => handleMouseEnter()} onMouseLeave={() => handleMouseLeave()} className="header">
          <div className="header-text">
            <div className="hidden-layer"><div className="header-text-name">adikpeto aristide</div></div>
            <div className="header-text-poste">Développeur web <br /> mobile and<br /> desktop</div>
            <div className="header-btn-container"><div className="header-btn">contacts us</div><div className="header-btn-border">contacts</div></div>
          </div>
          <div className="header-img">
            <div className="img-profil" style={{ transform: `rotate(-5deg) translate3D(${state.parallaxPos?.x}px , ${state.parallaxPos?.y}px, 0px)` }}></div>
            <svg id="visual" style={{ visibility: "hidden" }} viewBox="0 0 900 600" width="900" height="600" version="1.1"><g transform="translate(464.24159891029103 305.632296685166)"><path d="M132 -137.5C161.6 -102.3 169.8 -51.1 179.6 9.8C189.4 70.7 200.8 141.4 171.1 166.4C141.4 191.4 70.7 170.7 10.5 160.2C-49.7 149.7 -99.5 149.5 -142.1 124.5C-184.8 99.5 -220.4 49.7 -218.6 1.8C-216.9 -46.2 -177.7 -92.4 -135.1 -127.6C-92.4 -162.7 -46.2 -186.9 2.5 -189.3C51.1 -191.8 102.3 -172.6 132 -137.5" fill="#000"></path></g></svg>
          </div>
        </div>

        <div className="container-body">

          <div className="header-text-poste-custom" style={{ maxWidth: "30%", fontSize: "0.9rem", lineHeight: "1rem", marginTop: "10rem" }}>J’aimerais contribuer à la réalisation de vos sites, pages et applications à l’aide de ma frontend. mes experiences
            Développeur et applications à l’aide de ma connaissance du développement frontend et backend, mais aussi.</div>
          <div className="hidden" style={{ width: "60%", transform: "translateY(3rem)" }}>
            <div className="competence-title title-big" ref={titleRef}>mes experiences</div>
          </div>



          <div className="text-container">

            <div className="header-text-poste">
              {dataCompetence.map((item) => {
                return <Competence title={item.skill} description={item.description} />
              })}

            </div>


          </div>
          <div ref={dividerRef} className="img-divider">

            <div className="projet-text-container">
              <div className="title-big" style={{ wordBreak: "keep-all", wordWrap: "normal", color: "#161616" }}>
                MY <br />PR <br />OJ <br />ET <br />S
              </div>
              <div className="projet-container">
                <div className="projet m">

                </div>
                <div className="projet pm"></div>
                <div className="projet t">
                  <div className="small-title">This projet is my rencent projet.</div>
                  <div className="small">J’aimerais et applications à l’aide de ma frontend et backend, mais aussi.</div>
                </div>
                <div className="projet dp"></div>



              </div>
            </div>

          </div>


        </div>

      </Layout>
    </>

  )
}

export const Head = () => <title>Home page</title>
export default Home