import React, { useState, useEffect, useReducer,useRef } from 'react'
import animate from '../menu/animate'
import './Competence.scss'



const initialState = {
    opacity: 0,
    parallaxPos: { x: 0, y: -20 },  
}

function reducer(state, action) {
    switch (action.type) {

        case "CHANGE/OPACITY":

            return { ...state, opacity: action.payload }
            break;
        case "CHANGE/COORDINATES":
            return { ...state, parallaxPos: action.payload }
            break;

        default:
            throw new Error("Message")

    }
}
function Competence({title,description}) {
    
    const [state, dispatch] = useReducer(reducer, initialState)
    const listItem = useRef(null)
    const easeMethod = "linear"


    // Function

    const parallax = (event) => {
        const speed = 15
        const x = (window.innerWidth - event.pageX * speed) / 100
        const y = (window.innerHeight - event.pageY * speed) / 100


        dispatch({
            type: "CHANGE/COORDINATES",
            payload: { x: x, y: y }
        })
    }

    const handleMouseEnter = () => {
        handleOpacity(0, 1, 500)
        listItem.current.addEventListener("mousemove", parallax)
    }
    const handleOpacity = (initialOpacity, newOpacity, duration) => {
        animate({
            fromValue: initialOpacity,
            toValue: newOpacity,
            onUpdate: (newOpacity, callback) => {
                dispatch({ type: "CHANGE/OPACITY", payload: newOpacity })
                callback()
            },
            onComplete: () => { },
            duration: duration,
            easeMethod: easeMethod
        })
    }

 

  
    const handleMouseLeave = () => {
        listItem.current.removeEventListener("mousemove", parallax)
        handleOpacity(1, 0, 800)
        dispatch({
            type: "CHANGE/COORDINATES",
            payload: initialState.parallaxPos
        })

    }

    return (
        <>
            <div className='info-card' style={{ transform:`translate3D(${state.parallaxPos?.x}px , ${state.parallaxPos?.y}px, 0px)`,opacity:state.opacity}}>
                <div className='info-title'>Mes projet react native recents.</div>
                <div className='info-text'>
                     {description}
                </div>
                <div className='info-img'></div>
            </div>
            {/* <span ref={listItem} onMouseEnter={()=>handleMouseEnter()} onMouseLeave={()=>handleMouseLeave()} onClick={() => alert("Ok")} className="competence">{title}</span> */}
            <div ref={listItem}  className="competence">{title}</div>
        </>

    )
}

export default Competence